<template>
  <div class="apply">
    <van-tabs v-model="active" swipeable :class="{bor_b:active===1}" @click="tabClick_">
      <van-tab title="贷款">
        <Loan></Loan>
      </van-tab>
      <van-tab title="信用卡">
        <CreditCard></CreditCard>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import Loan from './Loan'
import CreditCard from './CreditCard'
export default {
  components: {
    Loan, CreditCard
  },
  data () {
    return {
      listBox: 'loan',
      active: this.$route.params.index
    }
  },
  methods: {
    tabClick_(index,title) {
      this.$router.push(`/apply/${index}`)
    }
  }
}
</script>
<style lang="scss" scoped>
.apply{
  position: relative;
  height: calc(100vh - .88rem);
  overflow: scroll;
  box-sizing: border-box;
  background-color: #fff;
}
.list_box{
  padding-top: .88rem;
}
</style>



